/*===== анимация для блока*/
.Wednesday_infoServer__text__3JT_Y {
    position: relative;
    /*margin: 300px auto 0;
    width: 500px;
    height: 250px;*/
    background: linear-gradient(0deg, #000, #272727);
}

.Wednesday_infoServer__text__3JT_Y:before, .Wednesday_infoServer__text__3JT_Y:after {
    content: '';
    position: absolute;
    left: -4px;
    top: -4px;
    background: linear-gradient(45deg, #fb0094, #0000ff, #00ff00, #ffff00, #ff0000, #fb0094,
    #0000ff, #00ff00, #ffff00, #ff0000);
    background-size: 400%;
    width: calc(100% + 8px);
    height: calc(100% + 8px);
    z-index: -1;
    -webkit-animation: Wednesday_steam__3AllK 20s linear infinite;
            animation: Wednesday_steam__3AllK 20s linear infinite;
}

@-webkit-keyframes Wednesday_steam__3AllK {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 400% 0;
    }
    100% {
        background-position: 0 0;
    }
}

@keyframes Wednesday_steam__3AllK {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 400% 0;
    }
    100% {
        background-position: 0 0;
    }
}

.Wednesday_infoServer__text__3JT_Y:after {
    -webkit-filter: blur(30px);
            filter: blur(30px);
}

